import React, {Component} from 'react'
import {connect} from 'react-redux'
import {formatTweet, formatDate} from '../utils/helpers'
import {TiHeartFullOutline, TiHeartOutline, TiArrowBackOutline} from 'react-icons/ti'
import {handleToggleLike} from '../actions/shared'
import {withRouter, Link, Redirect} from 'react-router-dom'
class Tweet extends Component{
    handleLike = (e,data)=>{
        e.preventDefault()
        console.log('dataaaa',data)
        this.props.dispatch(handleToggleLike(data))
        console.log('state after like button', this.props.state)
    }
    handleReply = (e,x)=>{
        e.preventDefault()
    }
    toParent = (e,id)=>{
        e.preventDefault()
        this.props.history.push(`tweet/${id}`)

    }
    re(replies){
        for(const r in replies){
            console.log('what the hhhh', this.props.history.location.pathname)
            if(this.props.history.location.pathname===`/tweet/${replies[r]}`){
                return true
            }
        }
        return false
    }

    render(){
        //console.log(this.props)
        //console.log('tweet componenet', this.props)
        //const name = this.props.state.tweets[this.props.id].author
        //const users = this.props.state.users
        //console.log('author', (users[name]).name)
        //console.log('name', name) 
        
        const {tweet, state} = this.props
        const authUser = this.props.state.authUser
        const {id, text, name,likes, replies,hasLiked, timestamp, replyingTo, parent, avatar} = tweet
        const keys  = Object.keys(tweet)

        // keys.forEach((key) => {
        //     console.log(`key: ${key} value: ${tweet[key]}`)
        // })
        if(tweet===null){
            return(<p>no such tweet</p>)
        }
        console.log('history', this.props, this.props.history.location.pathname)
        if(this.props.history.location.pathname===`/tweet/${id}`){
            console.log(true)

        }
        if(this.props.history.location.pathname===`/tweet/tweet/${id}`){
            return <Redirect to={`/tweet/${id}`}/>
        }else{
        return(
           
            <Link to={this.props.history.location.pathname===`/tweet/${id}`||(this.re(tweet.replies))?`${id}`:`tweet/${id}`} className="tweet">
            {/* <Link to={`tweet/${id}`} replace>     */}
                 <img className='avatar' src={avatar} alt={`the image of ${name}`}/>
            <div className='tweet-info'>
               
               <div>
                 <span>{name}</span>
                       {
                           <div>
                                {/* <h4 style={{color:'#000000'}}><b>{name}</b></h4> */}
                                <div>{formatDate(timestamp)}</div>
                                {parent!==null?<button className='replying-to' style={{fontSize:14}} onClick={(e)=>this.toParent(e, parent.id)}>replied to @{parent.author}</button>:''}
                           </div>
                       }   
                                    
               </div>
            
                
            
            <div className='tweet-info'>
                <p>{text}</p>
            </div>
            <div className='tweet-icons'>
                    <TiArrowBackOutline className='tweet-icon'/><span>{replies!==0?replies:''}</span>
                
                <button className="heart-button" onClick={(e)=>this.handleLike(e,{id,authUser,hasLiked})}>{hasLiked===false?<TiHeartOutline className='tweet-icon'/>:<TiHeartFullOutline className='tweet-icon' style={{color:'red'}}/>}</button>
                 <span>{likes!==0?likes:''}</span>
                </div>
            </div>
            </Link>
           
        );
    }
}}
function mapStateToProps(state, propss){
    const tweet = state.tweets[propss.id]
    //console.log('tweet', tweet, tweet.author, 'users',users)
    const author = state.users[tweet.author]
    //console.log('auther', author)
    const parent = tweet? state.tweets[tweet.replyingTo]:null
    //console.log('parent', parent)
    return {
        state:state,
        tweet: tweet?formatTweet(tweet,author,state.authUser, parent):null
    }
}

export default withRouter(connect(mapStateToProps)(Tweet))

