import React, {Component} from 'react'
import {NavLink} from 'react-router-dom'


export function Nav(){
    return(
        <div>
        <nav className='nav'>
        <ul>
            <li>
             <NavLink to='/' exact activeClassName='active' className='nav-item'>Home</NavLink>
          </li>
          <li>
          <NavLink to='/new' exact activeClassName='active' className='nav-item'>New Tweet</NavLink>
          </li>
        </ul>

        </nav>
        </div>

    )
}




