import React, {Component} from 'react'
import {connect} from 'react-redux'
import {handleAddTweet} from '../actions/shared'
import {Redirect} from 'react-router-dom'
class NewTweet extends Component{

    state={
        text:'',
        toHome: false
    }

    onChange = (e)=>{
        e.preventDefault()
        const value = e.target.value
        this.setState({text:value})
        
        //e.target.value = ''
    }
    handleSubmit = (e) =>{
        e.preventDefault()
        const author = 'tylermcginnis'
        const replyingTo = null
        const {text} = this.state
        const {dispatch, id} = this.props
        this.props.dispatch(handleAddTweet(text, id))
        this.setState(()=>(
            {text: '', toHome:this.props.id?false:true}
        ))

    }
    render(){
        const {text, toHome} = this.state
        if(toHome===true){
            return <Redirect to='/'/>
        }
        const textLeft= 280-text.length
        return(

            <div className='new-tweet'>
            
           
            <h3>Compose new Tweet</h3>
            <form onSubmit={this.handleSubmit}>
            <textarea className='textarea' placeholder='enter new tweet' rows='5' cols='50' name='newTweet' onChange={this.onChange} value={text} maxLength={280}/>
            {
                textLeft <= 100 && <div className='tweet-length'>{textLeft}</div> 
            }
            <button className='button' disabled={text===''} type='submit'>Submit</button>
            </form>
            </div>
        )
    }
}
export default connect()(NewTweet)


