import {ADD_TWEETS, TOGGLE_TWEET, ADD_TWEET} from '../actions/tweets'
import authUser from './authUser'


export default function tweets(state={}, action){
    switch(action.type){
        case ADD_TWEETS:
            return {...state, ...action.tweets}
        case TOGGLE_TWEET:
            {
                console.log('action.data',typeof(action.data.authUser),'state',state[action.data.id].likes,state[action.data.id].likes, state[action.data.id], state, action.data.id,'return state', {...state,
                    [action.data.id]: {
                      ...state[action.data.id],
                      likes: action.data.hasLiked === true
                        ? [...state[action.data.id].likes.filter((uid) => {uid !== action.data.authedUser})]
                        : state[action.data.id].likes.concat([action.data.authedUser])    
            }})

            // return Object.keys(state).map((id) => state.id !== action.data.id ? {...state[id]} :
            // Object.assign({}, state[id], { hasLikes: !state[id].hasLikes }))

            // return Object.assign({}, ...Object.keys(state).map((id) => id !== action.data.id ? {[id]:state[id]} :
            // Object.assign({}, state[id], { hasLikes: !state[id].hasLikes })))
            console.log('before return', action.data.authUser, state[action.data.id].likes)
               const {authUser} = action.data
               console.log('before return', authUser,action.data.authUser, state[action.data.id].likes)

               return {...state,
                [action.data.id]: {
                  ...state[action.data.id],
                  likes: action.data.hasLiked === true
                    ? [...state[action.data.id].likes.filter((uid) => uid !== authUser)]
                    : [...state[action.data.id].likes, authUser]    
        }}}
            case ADD_TWEET:
                const {tweet} = action
                let replyingTo={}
                if(tweet.replyingTo!==null){
                    replyingTo = {
                        [tweet.replyingTo]: {
                            ...state[tweet.replyingTo], replies: state[tweet.replyingTo].replies.concat([tweet.id])
                        }
                    }
                }
                return {
                    ...state, [action.tweet.id]:{
                        ...action.tweet
                    }, ...replyingTo
                }
    
            default :
                return state    
    }
}


