import React, {Component} from 'react'
import { connect } from 'react-redux'
import Tweet from './Tweet'
import NewTweet from './NewTweet'


class TweetPage extends Component{


    render(){

        const {id, replies} = this.props

        console.log('iddddddddddddddddddddddddd', id)
        console.log(this.props)
        return(
            <div>
            {this.props.state.tweets[id]?<Tweet id={id}/>:'loading'}
            {this.props.state.tweets[id]?<NewTweet id={id}/>:'loading'}
            <ul>
                {
                    replies.map((tweetId)=>(<li key={tweetId}><Tweet id={tweetId}/></li>))

                }
            </ul>
            </div>
            
        )
    }
}
function mapStateToProps(state, props){
    //console.log('state', state)
    const {id} = props.match.params
    const tweet = state.tweets[id]
    console.log('tweet',state.tweets)
    const replies = !tweet ?[]:tweet.replies.sort((a,b)=>state.tweets[b].timestamp-state.tweets[a].timestamp)
    return {
        state,
        id,
        replies,
    }
}

export default connect(mapStateToProps)(TweetPage)


