import React, { Component, Fragment } from 'react'
import {connect} from 'react-redux'
import {mountInitialData} from '../actions/shared'
import Dashboard from './Dashboard'
import LoadinBar from 'react-redux-loading'
import NewTweet from './NewTweet'
import TweetPage from './TweetPage'
import {Nav} from './Nav'
import {BrowserRouter as Router, Route} from 'react-router-dom'
class App extends Component {
  
  componentDidMount(){
    //console.log('component is mounted')
    this.props.dispatch(mountInitialData())
        //console.log('loading mounttttt', this.props.loading, this.props.state.authUser)

    //console.log(mountInitialData())
  }
  render() {
    //console.log('loading', this.props.loading)

    //console.log('loading', this.props.loading, this.props.state.authUser)
    if(this.props.loading){
      //console.log('loading', this.props.loading)
      return <p>loading</p>
    }
    else{
    return (
      <Fragment>
      <LoadinBar/>
      <Router>
      <div className="container">
      <Nav/>
      
      {/* 
      <NewTweet/>

      <Dashboard/> */}
      {this.props.loading?null:
      <div>
      <Route path='/new' exact component={NewTweet}/>

      <Route path='/' exact component={Dashboard}/>
      <Route path='/tweet/:id' component={TweetPage}/>
      {/* <TweetPage match={{params:{id:"nnvkjqoevs8t02lzcc0ky"}}} /> */}

      </div>
      
      }
      </div>
      </Router>
      </Fragment>
    )
  }
  }
}
function mapStateToProps(state){
  return {
    state:state,
    loading:state.authUser===null
  }
}

export default connect(mapStateToProps)(App)


