export const ADD_TWEETS = 'ADD_TWEETS'
export const TOGGLE_TWEET = 'TOGGLE_TWEET'
export const ADD_TWEET = 'ADD_TWEET'


export function addTweets(tweets){
    return {
        type:ADD_TWEETS,
        tweets,
    }
}
export function toggleLike(data){
    return{
        type:TOGGLE_TWEET,
        data:data
    }
}
export function addTweet(tweet){
    return {
        type:ADD_TWEET,
        tweet,
    }
}




