import {addTweets, toggleLike, addTweet} from './tweets'
import {addUsers} from './users'
import {getInitialData, saveTweet} from '../utils/api'
import {setAuthUser} from './authUser'
import {showLoading, hideLoading} from 'react-redux-loading'
import {saveLikeToggle} from '../utils/api'
//import {showLoading, hideLoading} from 'react-redux-loading'

export const ADD_DATA = 'ADD_DATA'
export const authUserId = 'tylermcginnis'

export function handleToggleLike(data){
    return(dispatch,getState)=>{
        //dispatch(showLoading())
        //const {authUser} = getState
       return saveLikeToggle(data).then(()=>{
           
        console.log('tweets',data)
        dispatch(toggleLike(data))
        //dispatch(hideLoading())
            //dispatch(addTweets(tweets))
        })
    }
}
export function mountInitialData(){
    return (dispatch, getState)=>{
        //console.log('getState', getState())
        return getInitialData().then(({users, tweets})=>{
            dispatch(showLoading())
            dispatch(addUsers(users))

            dispatch(setAuthUser(authUserId))

            dispatch(addTweets(tweets))
            dispatch(hideLoading())
        //console.log('state:', getState().tweets)
        //console.log('state:', getState().users)

        //console.log('state:', getState().authUser)

    })
}
}
export function handleAddTweet(text, replyingTo){
    return(dispatch,getState)=>{
        const {authUser} = getState()
        return saveTweet({text, author:authUser, replyingTo})
        .then((tweet)=>{
            dispatch(showLoading())
            dispatch(addTweet(tweet))
            dispatch(hideLoading())})
        // .then((result)=>console.log('result', result))
    }
}


